/*
|--------Responsive Simple Navbar-----------|
|            version: 1.05                 |                            
|                                          | 
|------------------------------------------|
*/


:root{
  /*Colors*/
  --navbarBackground:  #54a3e4;
  --navbarItemHoverColor: #ffff;
  --navbarActiveBackground: #90c4ee;
  --navLinksColor: white;
  --navBarLogoColor: white;
  --hamburgerColor: white;

  /*Font Size*/
  --navItemFontSize: 1.1rem;
  --logoSize: 1.7rem;
  --navLinksMobileFontSize: 1.2rem;


  /*Navbar Height*/
  --navbarHeight: 70px;
  

}

.navbar {
  width: 100%;
    background: #54a3e4;
    background: var(--navbarBackground);
    height: 70px;
    height: var(--navbarHeight);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.1rem;
    font-size: var(--navItemFontSize);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    box-shadow: 0px 3px 12px  rgba(27, 28, 29, 0.795);
  }

  .hamBurger{
      color: white;
      color: var(--hamburgerColor);
  }
  
  .navbar-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 70px;
    height: var(--navbarHeight);
  }
  
  .container {
    z-index: 1;
    width: 100%;
    max-width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: 20px;
  }
  
  .navbar-logo {
    color: white;
    color: var(--navBarLogoColor);
    margin-left: 1rem;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.7rem;
    font-size: var(--logoSize);
    display: flex;
    align-items: center;
  }
  
  .navbar-icon {
    margin-right: 0.5rem;
  }
  
  .nav-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
  }
  
  .nav-item {
    height: 70px;
    height: var(--navbarHeight);
    border-bottom: 2px solid transparent;
  }
  
  .nav-item:hover {
    border-bottom: 3px solid #ffff;
    border-bottom: 3px solid var(--navbarItemHoverColor);
  }
  
  .nav-links {
    color: white;
    color: var(--navLinksColor);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 85%;
  }
  
  
  
  .menu-icon {
    display: none;
  }

  @media screen and (max-width: 365px) {

    li:first-child{
      margin-top: 4rem !important;
    }
  }

  
  @media screen and (max-width: 1300px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      width: 90%;
      height: 100vh;
      position: absolute;
      top: 52px;
      left: -70%;
      opacity: 1;
      transition: 0.3s ease-in-out;
    }
  
    .nav-menu.active {
      background: #90c4ee;
      background: var(--navbarActiveBackground);
      left: 0%;
      opacity: 1;
      transition: 0.4s ease-in-out;
      z-index: 1;
    }
  
    .nav-links {
      /*margin-top: 0.8rem !important;*/
      font-size: 1.2rem;
      font-size: var(--navLinksMobileFontSize);
      text-align: center;
      height: 0%;
    }

    li:first-child{
      margin-top: 1rem;
    }

    li:last-child{
      margin-bottom: 10%;
    }
  
    .nav-links:hover {
      font-size: 1.3rem;
      color: #ffff;
      color: var(--navbarItemHoverColor);
      transform: scale(1.05);
      font-weight: bold;
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 90%;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 120px;
    }
  }
